.descriptionBlock {
    padding: 30px 50px;
    background-color: #fff;
    height: 100%;
    box-sizing: border-box;
    border-bottom: 1px solid #f0f0f0;
    display: flex;
    flex-flow: row nowrap;
}

.photo {
    flex: 0 0 50%;
    display: block;
    text-align: center;
    max-width: 30%;
    flex-flow: row nowrap;
    align-items: center;
}

.status {
    margin: 0 10px;
    font-size: 16px;
}

.wrapperMainPhoto {

}

.desc {
    flex: 0 0 50%;
    padding-left: 50px;
}

.profileImg {
    height: 200px;
}

.mainPhoto {
    max-width: 140px;
    border-radius: 70px;
    margin: 20px;
}

.contact {
    padding-bottom: 10px;
}

.formDataModule {
    display: flex;
    flex-flow: column nowrap;
    justify-content: center;
}

.buttons {
    padding: 10px;
    width: 100px;
    text-align: center;
    background-color: #5692d7;
    color: #fff;
    border: none;
    transition: .3s all;
    border-radius: 3px;
    
}

.editPhoto {
    padding: 5px;
    border-radius: 3px;
    border: 1px solid #5692d7;
    margin: 5px;
    display: none;
}

.selectPhoto {
     padding: 10px;
     background-color: #5692d7;
     border-radius: 3px;
     color: #fff;
     display: block;
     margin: 10px;
     font-size: 15px;
     transition: .2s all;
     font-weight: 600;
 }

.selectPhoto:hover {
    background-color: #407bbd;
    cursor: pointer;
}
