.postsBlock{

}

.posts{
    padding: 10px;
}

.title {
    margin: 20px;
    display: block;
}

.blockAddPost {
    padding: 10px;
    display: flex;
    flex-flow: row nowrap;
}

.postsTitles {

}

.addPostButton {
    padding: 10px;
    width: 100px;
    text-align: center;
    background-color: #5692d7;
    color: #fff;
    border: none;
    margin: 0 10px;
    transition: .3s all;
    border-radius: 3px;
}

.areaAddPost {
    border-radius: 3px;
    border: 1px solid #5692d7;
    width: 100%;
}

.addPostButton:hover {
    background-color: #407bbd;
}