.item {
}

.item img {
    width: 50px;
    height: 50px;
    border-radius: 30px;
}

.message {
    margin: 0 20px;
    padding: 5px;
    background-color: #fff;
    border: 1px solid #f0f0f0;
    border-radius: 5px;
    color: #000 !important;
}

.wrapper {
    display: flex;
    flex-flow: row nowrap;
}

.likes {
    color: #5692d7;
    background-color: #f5f5f5;
    border: 1px solid #f0f0f0;
    padding: 5px 10px;
    display: inline-block;
    margin: 5px 0;
}